<template>
<div class="page"><div class="page-content" @scroll="onScroll">
  
    <f7-navbar back-link="–ù–∞–∑–∞–¥" >
    <f7-nav-center sliding>{{pageTitle}}</f7-nav-center>
      <f7-nav-right>
        <f7-link @click="filterOpenPopup()" ><f7-icon f7="filter" color="red"></f7-icon></f7-link>
      </f7-nav-right>
      <f7-subnavbar sliding>
      <f7-searchbar
        cancel-link="–û—Ç–º–µ–Ω–∞"
        search-list="#search-list"
        placeholder="–ü–æ–∏—Å–∫ —Ç–æ—á–µ–∫"
        :customSearch="true"
        :clear-button="true"
        @searchbar:search="onSearch"
        @searchbar:enable="onEnable"
        @searchbar:disable="onDisable"
        @searchbar:clear="onClear"
      ></f7-searchbar>
    </f7-subnavbar>
    </f7-navbar>
    <f7-block-title></f7-block-title>
    <f7-card class="point-list" >
      <f7-card-header>–¢–æ—á–∫–∏</f7-card-header>
      <f7-card-content>
        <f7-list class="searchbar-not-found">
          <f7-list-item title="–ó–ª–µ–º–µ–Ω—Ç–æ–≤ –Ω–µ –Ω–∞–π–¥–µ–Ω–æ"></f7-list-item>
        </f7-list>
        <f7-list media-list>
          <f7-list-item
            v-for="(item, key) in $root.points"
            :link="'/detail/'+item.ID+'/'"
             class="searchbar-found" 
             id="search-list"
             >
            <div slot="inner-start" class="item-title-row"><div class="item-title">{{item.NAME}}</div></div>
            <div slot="inner-start" class="item-subtitle">–≥.{{item.GOROD}}</div>
            <div class="item-text" slot="inner">{{item.ADRES}}</div>
            <img slot="media" src='../assets/images/akb.png' height="23" width="23">
          </f7-list-item>
        </f7-list>
      </f7-card-content>
    </f7-card>
  </div></div>
</template>

<style>
  .point-list .list-block .item-text{height: auto;}
</style>

<script>
// import PointItem from "../components/PointItem"
import {convert} from '../utils'

export default {

  prop:['filter'],

  data: function(){
    return{
      pageTitle: "",
      loading: false,
      postParams: {
        device: this.$root.deviceId,
        key: this.$root.user.token,
        set_filter: true,
      },
      categoryPath: "",
      countUpload: this.$root.pageSize,
    }
  },
  methods:{
    setPageTitle: function(){

      switch(this.$route.path) {
        case '/point/':
          this.pageTitle = "–û–±—Ä–∞–±–æ—Ç–∞–Ω–Ω—ã–µ"
        break;
        case '/tasks/':
          this.pageTitle = "–ù–µ–æ–±—Ä–∞–±–æ—Ç–∞–Ω–Ω—ã–µ"
        break;
      }
    },
    onScroll: function (e) {

      const wrapper = e.target;
      const list = wrapper.children[1];
//console.log("list", list)
      const scrollTop = wrapper.scrollTop;
      const wrapperHeight = wrapper.offsetHeight;
      const listHeight = list.offsetHeight;
//console.log("listHeight", listHeight)
//console.log("wrapperHeight", wrapperHeight)
      const diffHeight = listHeight - wrapperHeight;

 //console.log(diffHeight, scrollTop)
      if(diffHeight <= scrollTop && !this.loading && (this.$root.points.length % this.$root.pageSize) == 0){
        this.$root.page = this.$root.page+1;
        this.load();
      }
    },
    load: function(searchContent){

      this.loading = true;
      this.$f7.showIndicator();

      const options = {
        params: this.postParams
      };
      options.params.page = this.$root.page;
      options.params.pageSize = this.$root.pageSize;
      if(typeof searchContent !== "undefined"){
        options.params.searchName = searchContent;
      }

      this.$http.get(this.$root.startUrl+'point'+this.$root.categoryPath+'?'+convert(this.$root.filterChek), options).then(function(res){
        const data = JSON.parse(res.bodyText)
        //–î–æ–±–∞–≤–ª—è–µ–º —Ç–æ—á–∫–∏ –∫ —É–∂–µ –∏–º–µ—é—â–∏–º—Å—è
        if(typeof data.point !== "undefined"){
          this.$root.points = this.$root.points.concat(data.point);
        }else{
          this.$root.points = [];
        }

        this.loading = false;
        this.$f7.hideIndicator();
        return false;
      }, function(err){
        console.log(err);
        this.loading = false;
        this.$f7.hideIndicator();
        return false;
      });
    },
    filterOpenPopup: function(){
      this.$root.$emit('filterOpenPopup', {categoryPath:this.categoryPath});
       this.$f7.popup('#filter');
       this.filterLoad()
    },
    tester: function(){
      console.log("tester-list")
    },
    filterLoad: function(){
      this.$f7.showIndicator();

      const options = {
        params: this.postParams
      };

      this.$http.get(this.$root.startUrl+'filter'+this.$root.categoryPath+'?'+convert(this.$root.filterChek), options).then(function(res){
        const data = JSON.parse(res.bodyText)
        this.$root.filter = data.rows;
        this.$root.countElement = data.count;

        this.$f7.hideIndicator();
        return false;
      }, function(err){
        console.log(err);
        this.$f7.hideIndicator();
        return false;
      });

    },
    chekCategory: function(){
      
      //–∑–±—Ä–∞—Å—ã–≤–∞–µ–º –≥–ª–æ–±–∞–ª—å–Ω—ã–µ —Ç–æ—á–∫–∏ –∏ —Ñ–∏–ª—å—Ç—Ä
      this.$root.points = [];
      this.$root.filterChek = {};

      //–æ–±–Ω–æ–≤–ª—è–µ–º –≥–ª–æ–±–∞–ª—å–Ω–æ–µ –∑–Ω–∞—á–µ–Ω–∏–µ –∫–∞—Ç–µ–≥–æ—Ä–∏–π
      this.$root.categoryPath = this.$route.path;
      
      //–¥–ª—è —Å–µ–Ω—Å—É—Å–∞ –∫–∞—Ç–µ–≥–æ—Ä–∏–∏ - —ç—Ç–æ –∑–Ω–∞—á–µ–Ω–∏–µ —Ñ–∏–ª—å—Ç—Ä–∞ –ø–æ –æ–¥–Ω–æ–º—É —Å–≤–æ–π—Å—Ç–≤—É
      switch(this.$route.path) {
        case '/point/':
          this.$root.filterChek["arrFilter_423_3233089245"] = "Y";
        break;
        case '/tasks/':
          this.$root.filterChek["arrFilter_423_1130791706"] = "Y";
        break;
      }
    },

      onSearch: function (query, found) {
        this.$root.points = [];
        this.$root.page = 1;
        if(!this.loading){
          this.load(query);
        }
        console.log('search', query);
      },
      onClear: function (event) {
        console.log('clear');
      },
      onEnable: function (event) {
        console.log('enable');
      },
      onDisable: function (event) {
        console.log('disable');
      },


  },
  // components:{
  //   FilterPage
  // },
  created:function(){

  //—Å–±—Ä–∞—Å—ã–≤–∞–µ–º —Ñ–∏–ª—å—Ç—Ä
    this.filter = {},
    this.filterCheked = {}

    //—á–µ–∫–∞–µ–º –∫–∞—Ç–µ–≥–æ—Ä–∏—é
    this.chekCategory();

    //–ó–∞–ø–∏—Å—ã–≤–∞–µ–º –∑–∞–≥–æ–ª–æ–≤–∫–∏
    this.setPageTitle();

    //–ø–æ–¥–≥—Ä—É–∂–∞–µ–º —Å–ø–∏—Å–æ–∫ —Ç–æ—á–µ–∫ –∫–∞—Ç–µ–≥–æ—Ä–∏–∏ –∏ –∑–∞–≥–æ–ª–æ–≤–∫–∏
    this.load();
  }
}
</script>


