<template>

    <f7-card>
      <f7-card-header>
        <div style="margin: 0 auto;">
          <img src="../assets/images/header_logo2.png" style="height:39px;width:auto;">
        </div>
      </f7-card-header>
      <f7-card-content class="auch__form">

            <f7-card>
              <f7-card-header>
                <div class="color-red" style="font-size: 20px;text-transform: uppercase;">–í—Ö–æ–¥</div>
              </f7-card-header>
              <f7-card-content>
                <f7-list form>
                  <f7-list-item
                    media=""
                    v-show="phoneShow"
                    >
                    <f7-icon f7="phone" color="red" slot="media"></f7-icon>
                    <div class="item-input">
                      <input v-mask="'+7(###) ###-##-##'" type="tel" v-model="phone" slot="inner" placeholder="—Ç–µ–ª–µ—Ñ–æ–Ω"></input>  
                    </div>
                  </f7-list-item>
                  <f7-list-item
                    media=""
                    v-show="codeShow"
                    >
                    <f7-icon f7="lock" color="red" slot="media"></f7-icon>
                    <div class="item-input">
                      <input v-mask="'####'" type="tel" v-model="code" slot="inner" placeholder="1234"></input>  
                    </div>
                  </f7-list-item>
                </f7-list>
                
              </f7-card-content>
              <f7-card-content v-show="isValid">
                <p class="color-red">{{erText}}</p>
              </f7-card-content>
              <f7-card-content>
                <f7-button @click="actionAutch()" big color="red">{{nameBottom}}</f7-button>
              </f7-card-content>
            </f7-card>

      </f7-card-content>
    </f7-card>


</template>
<style>
  .auch__form> div.card-content-inner{padding: 5px}
</style>
<script>

export default {
  data: function(){
    return{
      phone: "",
      code: "",
      phoneShow:true,
      codeShow: false,
      isValid:false,
      erText:"–ù–µ–≤–µ—Ä–Ω—ã–π —Ñ–æ—Ä–º–∞—Ç —Ç–µ–ª–µ—Ñ–æ–Ω–∞",
      nameBottom:"–í–æ–π—Ç–∏"
    }
  },
  methods:{
    setShow: function() {
      //this.$f7.popup('#popup')
      this.phoneShow = !this.phoneShow;
      this.codeShow = !this.codeShow;
      this.nameBottom = "–ü–æ–¥—Ç–≤–µ—Ä–¥–∏—Ç—å –∫–æ–¥"
    },
    actionAutch: function() {
      //–æ—Ç–ø—Ä–∞–≤–ª—è–µ–º —Ç–µ–ª–µ—Ñ–æ–Ω
      if(this.phoneShow && !this.codeShow){
        if(this.phone.replace(/[^0-9]/gim,'').length < 10){
          this.isValid = true;
          this.erText = "–ù–µ–≤–µ—Ä–Ω—ã–π —Ñ–æ—Ä–º–∞—Ç —Ç–µ–ª–µ—Ñ–æ–Ω–∞";
          return false;
        }
        //–æ—Ç–ø—Ä–∞–≤–ª—è–µ–º –∑–∞–ø—Ä–æ—Å –∫–æ–¥–∞ –ø–æ–¥—Ç–≤–µ—Ä–∂–¥–µ–Ω–∏—è
        this.queryAutch();
        return false;
      }
      //–æ—Ç–ø—Ä–∞–≤–ª—è–µ–º –∫–æ–¥ –ø–æ–¥—Ç–≤–µ—Ä–∂–¥–µ–Ω–∏—è
      if(!this.phoneShow && this.codeShow){
        //–≤–∞–ª–∏–¥–∞—Ü–∏—è —Ç–µ–ª–µ—Ñ–æ–Ω
        if(this.phone.replace(/[^0-9]/gim,'').length < 10){
          this.isValid = true;
          this.erText = "–ù–µ–≤–µ—Ä–Ω—ã–π —Ñ–æ—Ä–º–∞—Ç —Ç–µ–ª–µ—Ñ–æ–Ω–∞";
          //–º–µ–Ω—è–µ–º –≤–∏–¥ –æ–∫–Ω–∞ –Ω–∞ –≤–≤–æ–¥ —Ç–µ–ª–µ—Ñ–æ–Ω–∞
          this.phoneShow = !this.phoneShow;
          this.codeShow = !this.codeShow;
          this.nameBottom = "–í–æ–π—Ç–∏"
          return false;
        }
        //–≤–∞–ª–∏–¥–∞—Ü–∏—è –∫–æ–¥–∞ –ø–æ–¥—Ç–≤–µ—Ä–∂–¥–µ–Ω–∏—è
        if(this.code.replace(/[^0-9]/gim,'').length < 4){
          this.isValid = true;
          this.erText = "–ù–µ–≤–µ—Ä–Ω—ã–π —Ñ–æ—Ä–º–∞—Ç - –∫–æ–¥ –ø–æ–¥—Ç–≤–µ—Ä–∂–¥–µ–Ω–∏—è";
          return false;
        }
        //–æ—Ç–ø—Ä–∞–≤–ª—è–µ–º –∑–∞–ø—Ä–æ—Å –Ω–∞ –∞–≤—Ç–æ—Ä–∏–∑–∞—Ü–∏—é
        this.query–°onfirm();
        return false;
      }
    },
    queryAutch:function(){

      const options = {
        params: {
          phone: this.phone.replace(/[^0-9]/gim,''),
          device: this.$root.deviceId,
          key:"",
        }
      };
      this.$http.get(this.$root.startUrl+'autch/?step=code&type=phone', options).then(function(res){
        const data = JSON.parse(res.bodyText)

        if(data.code == "ok" && typeof data.errors === "undefined"){
          //–≤ —Å–ª—É—á–∞–µ —É—Å–ø–µ—Ö–∞
          this.isValid = false;
          this.setShow();
        }

        if(typeof data.errors !== "undefined"){
          this.isValid = true;
          this.erText = data.errors;
        }

        return false;
      }, function(err){
        //–æ—à–∏–±–∫–∞ —Å—Ä–µ–¥–∏–Ω–µ–Ω–∏—è
        this.isValid = true;
        this.erText = "–û—à–∏–±–∫–∞ —Å–æ–µ–¥–∏–Ω–µ–Ω–∏—è —Å —Å–µ—Ä–≤–µ—Ä–æ–º";
        return false;
      });
    },
    query–°onfirm:function(){

      const options = {
        params: {
          phone: this.phone.replace(/[^0-9]/gim,''),
          code: this.code.replace(/[^0-9]/gim,''),
          device: this.$root.deviceId,
          key:"",
        }
      };

      this.$http.get(this.$root.startUrl+'autch/?step=autch&type=phone', options).then(function(res){
        const data = JSON.parse(res.bodyText)

        if(typeof data.token !== "undefined" && typeof data.errors === "undefined"){
          //–≤ —Å–ª—É—á–∞–µ —É—Å–ø–µ—Ö–∞
          this.isValid = false;
          this.setAutch(data);
        }

        if(typeof data.errors !== "undefined"){
          this.isValid = true;
          this.erText = data.errors;
        }

        return false;

      }, function(err){
        //–æ—à–∏–±–∫–∞ —Å—Ä–µ–¥–∏–Ω–µ–Ω–∏—è
        this.isValid = true;
        this.erText = "–û—à–∏–±–∫–∞ —Å–æ–µ–¥–∏–Ω–µ–Ω–∏—è —Å —Å–µ—Ä–≤–µ—Ä–æ–º";
        return false;
      });
    },
    setAutch:function(data){
      //–æ–±–Ω–æ–≤–ª—è–µ–º –≥–ª–æ–±–∞–ª—å–Ω—ã–µ –¥–∞–Ω–Ω—ã–µ –æ –ø–æ–ª—å–∑–∞–∫–µ
      this.$root.user = {
        token: data.token || "",
        cart: data.cart || "",
        name: data.name || "",
        rights: data.rights || "",
      };

      //–∑–∞–ø–∏—Å—ã–≤–∞–µ–º –≤ localStorage –∏–Ω—Ñ—É –æ –ø–æ–ª—å–∑–∞–∫–µ
      localStorage.setItem("sensus_authorize_key", JSON.stringify(this.$root.user));

      //–ø–µ—Ä–µ–∫–ª—é—á–∞–µ–º –≥–ª–∞–≤–Ω—É—é —Å—Ç—Ä–∞–Ω–∏—Ü—É –Ω–∞ –æ—Å–Ω–æ–≤–Ω—É—é
      this.$root.isAutch = true;
      this.$root.StartPege = 'MainPage';
    },
  }
}
</script>


